body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.MuiAppBar-colorPrimary{color:#ff9800;background-color:#ec7907 !important}.news-card .MuiPaper-root{box-shadow:4px 4px 10px rgba(0,0,0,0.2) !important;margin:20px 15px;min-height:250px;background-color:#f0f0f0;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:center;z-index:2}.news-card a{text-decoration:none}.news-card .MuiCardContent-root{background-color:#fff;height:50%;margin-top:0px}.news-card .card-header{min-height:40px;background-color:#ec7907;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;padding:10px;color:#f0f0f0}.news-card .card-header h4{margin-left:5px;margin-right:5px;opacity:0.6}.news-card .card-header h3{text-transform:uppercase;opacity:0.8}
